.benefits__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  text-decoration: none;
  color: #000000;
  margin: 0;
  margin-bottom: 42px;
}

.benefits__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.benefits__text {
  font-family: "Lato", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
  color: #000000;
  margin: 0;
  padding: 0;
  text-align: left;
  min-height: 60px;
  max-width: 200px;
}

.benefits__item {
  display: flex;
  gap: 20px;

  &::before {
    content: "";
    width: 60px;
    height: 60px;
    background: #68b738;
    display: flex;
    align-items: center;
    justify-content: center;
    background-position: center;
    background-repeat: no-repeat;
  }

  &--leaf::before {
    background-image: url("../img/sprite.svg#benefits_leaf");
    background-size: 30px 32px;
  }

  &--powder::before {
    background-image: url("../img/sprite.svg#benefits_powder");
    background-size: 26px 29px;
  }

  &--cultery::before {
    background-image: url("../img/sprite.svg#benefits_cultery");
    background-size: 25px 30px;
  }

  &--watch::before {
    background-image: url("../img/sprite.svg#benefits_watch");
    background-size: 26px 31px;
  }
}

.benefits__wrapper {
  margin-bottom: 48px;
  padding: 0 20px;
}

@media (min-width: $tablet-width) {
  .benefits__title {
    font-family: "Oswald", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 60px;
    line-height: 60px;
    text-decoration: none;
    color: #000000;
    margin: 0;
    margin-left: 11px;
    margin-bottom: 32px;
  }

  .benefits__list {
    display: grid;
    grid-template-columns: 247px 249px;
    grid-template-rows: 280px 280px;
    row-gap: 37px;
    column-gap: 137px;

    padding-left: 11px;
    padding-bottom: 47px;
    counter-reset: list;
  }

  .benefits__wrapper {
    border-bottom: 1px solid #e6e6e6;
    margin-bottom: 43px;
  }

  .benefits__text {
    margin: 0;
    bottom: 57px;
    z-index: 2;
    font-family: "Lato", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #444444;
    max-width: 245px;
  }

  .benefits__item {
    padding-top: 40px;
    position: relative;
    flex-direction: column;
    gap: 31px;

    &::after {
      position: absolute;
      right: 0;
      top: 0;
      width: 134px;
      font-family: "Oswald", "Arial", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 280px;
      line-height: 280px;
      text-align: center;
      color: #f2f2f2;
      counter-increment: list 1;
      content: counter(list);
    }

    &::before {
      width: 80px;
      height: 80px;
    }
  }
}

@media (min-width: $desktop-width) {
  .benefits__wrapper {
    margin: 0 106px 69px 110px;
    border: none;
    padding: 0;
  }

  .benefits__list {
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-template-rows: 280px;
    row-gap: 0;
    padding: 0;
    justify-content: space-between;
    column-gap: 0;
  }

  .benefits__title {
    margin: 0;
    margin-bottom: 32px;
    padding-top: 7px;
  }

  .benefits__item {
    gap: 28px;
  }
}
